.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 180px;
}
.button {
  outline: none;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  width: 130px;
  border-radius: 15px;
  background: linear-gradient(to right,rgb(199, 233, 255),rgba(5, 105, 255, 0.751) );
  color: white;
  font-size: 16px;
  padding: 10px;
}
.button:hover{
  border: 1px solid white;
  height: 22px;
  width: 130px;
  transition:0.5s;
}
@media (max-width:768px) {
  .button{
    height: 25px;
    padding: 8px;
    width: 130px;
  }
}
