.container {
  display: flex;
  justify-content: space-between;
}

.icon{
  font-size: 35px;
  color: rgb(0, 109, 211);;
}
.middle_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 20px;
  justify-content: space-around;
  color: white;
  box-shadow: 0px 5px 10px 5px rgb(165, 165, 165);
  background: linear-gradient(to bottom, rgba(13, 133, 252, 0.676), rgb(0, 123, 255));
  width: 35%;
  height: 550px;
  border-radius: 20px;
}

.right_bar {
  display: flex;
  background: linear-gradient(to bottom, rgb(3, 124, 253), rgb(102, 168, 255));
  min-height: 90vh;
  width: 50px;
  border-radius: 5px;
}




@media screen and (max-width: 768px) {
  .container {
    min-height: 77.5vh;
    align-items: center;
    justify-content: center;
  }
  .right_bar {
    display: none;
  }

  .middle_container {
    padding: 0px;
    display: flex;
    justify-content: center;
    width: 94%;
    height: 550px;
  }
  .middle_current_container {
    width: 100%;
  }
  .profile_image {
    border-radius: 20px;
    height: 60px;
    width: 60px;
    margin: 30px 0px 30px 3px;
  }
  .profile_container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
