.container {
  height: 650px;
}
.text_container {
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.credit_card_container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  width: 350px;
  height: 200px;
  background-color: rgba(255, 255, 255, 0.968);
  border-radius: 20px;
}
.credit_card_header{
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    background-color: rgb(17, 120, 255);
}
.credit_card_details {
  font-size: 18px;
  font-weight: 500;
  text-align: center;
 color : rgb(57, 143, 236);
}

@media screen and (width <768px) {
  .credit_card_container{
    width: 300px;
  }
}
