.background {
  background-color: black;
  width: 100%;
  height: 100%;
  position: fixed;
  opacity: 0.5;
}
.container {
  transition: 500ms;
  position: fixed;
  height: 95%;
  width: 15%;
  display: flex;
  justify-content: space-around;
  padding-left: 20px;
  flex-direction: column;
  background: linear-gradient(to top, rgb(0, 123, 255), rgb(106, 168, 255));
 
}
.container_inactive {
  transition: 500ms;
  width: 0%;
}

@media screen and (max-width:768px) {
  .container{
    height: 85%;
    padding-left: 10px;
    width: 55%;
    border-radius: 10px;
  }
}